<template>
  <i class="iconfont icon-container" :class="getType"></i>
</template>

<script>
const classMap = {
  design: "icon-websj",
  psd: "icon-ps",
  fe: "icon-qianduankaifa",
  be: "icon-houduankaifa",
  tag: "icon-tag",
  qq: "icon-iconfontqq1",
  wechat: "icon-weixin",
  email: "icon-youxiang",
  github: "icon-huaban88",
  blog: "icon-boke",
  list: "icon-caidan"
}
export const types = Object.keys(classMap);
export default {   
  props: {
    type: {
      type: String,
      required: true
    }
  },
  computed: {
    getType() {
      return classMap[this.type];
    }
  }
}
</script>

