  components: {
    Page1,
    Page2,
    Page3,
    Page4,
    Page5,
    Page6,
    Icon,
  },
  methods: {
    handleClick() {
      if(this.$refs.nav.style.opacity == 1) {
        this.$refs.nav.style.opacity = 0;
        this.$refs.nav.style.height = 0;
      } else {
        this.$refs.nav.style.opacity = 1;
        this.$refs.nav.style.height = 250 + "px";
      }
    },
  },
  mounted() {
    particlesJS.load("particles", "/particles.json", function () {});
  },
  data() {
    return {
      isShow: false,
      isLoading: false,
    };
  },
  created() {
    const hash = location.hash;
    location.hash = "";
    setTimeout(() => {
      location.hash = hash;
    }, 50);
  },
};
</script>

